<template>
  <div class="userSection">
    <div class="content" slot="trigger">
      <div class="userAvatar">
        <img src="@/assets/img/avatar.png" alt="">
      </div>
      <div class="userName">{{ $store.state.auth.user.fullName }}</div>
      <b-tooltip label="Sair" 
        type="is-light"
        position="is-bottom">
        <div class="logout" v-on:click.stop="logout()">
          <i class="fas fa-sign-out-alt"></i>
        </div>
      </b-tooltip>

      
    </div>
  </div>
</template>

<script>
  export default {
    name: 'user-section',

    methods: {
      logout(){
        this.$store.dispatch('logout')
        .then(() => {
          this.$router.push('/login')
        })
        .catch((err) => {
          console.log(err)
          
          this.$toast.open({
            message: 'Erro ao efetuar o logout.',
            type: 'is-danger',
            position: 'is-bottom'
          })
        })
      }
    },
    
  }
</script>

